<template>
  <div class="add-customer container">
    <div class="row mt-4">
      <Alert class="w-100 text-center" v-if="alert" :message="alert" type="danger" />
    </div>
    <div class="row border-bottom mt-4">
      <h1>Add Customer</h1>
    </div>
    <div class="row mt-4">
      <form @submit.prevent="addCustomer" class="w-100">
        <div class="container bg-light p-4 mb-4 rounded">
          <h5>Customer Info</h5>
          <div class="form-group">
            <label for="first-name">First Name</label>
            <input type="text" id="first-name" class="form-control" v-model="customer.first_name">
          </div>
          <div class="form-group">
            <label for="last-name">Last Name</label>
            <input type="text" id="last-name" class="form-control" v-model="customer.last_name">
          </div>
        </div>
        <div class="container bg-light p-4 mb-4 rounded">
          <h5>Customer Contact</h5>
          <div class="form-group">
            <label for="phone">Phone</label>
            <input type="text" id="phone" class="form-control" v-model="customer.phone">
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <input type="email" id="email" class="form-control" v-model="customer.email">
          </div>
        </div>
        <div class="container bg-light p-4 mb-4 rounded">
          <h5>Customer Location</h5>
          <div class="form-group">
            <label for="address">Address</label>
            <input type="text" id="address" class="form-control" v-model="customer.address">
          </div>
          <div class="form-group">
            <label for="city">City</label>
            <input type="text" id="city" class="form-control" v-model="customer.city">
          </div>
          <div class="form-group">
            <label for="state">State</label>
            <input type="text" id="state" class="form-control" v-model="customer.state">
          </div>
        </div>
        <button type="submit" class="btn btn-light w-100 font-weight-bold mb-5">Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Alert from '../components/Alert';

export default {
  name: "AddCustomer",
  components: {
    Alert
  },
  data() {
    return {
      customer: {},
      alert: ''
    }
  },
  methods: {
    async addCustomer() {
      let customer = {
        first_name: this.customer.first_name,
        last_name: this.customer.last_name,
        phone: this.customer.phone,
        email: this.customer.email,
        address: this.customer.address,
        city: this.customer.city,
        state: this.customer.state
      }
      try {
        await axios.post('api/v1/customer', customer);
        this.$router.push({ path: '/', query: { alert: {message: 'Success add new customer', type: 'success' }}});
      } catch (err) {
        this.alert = 'Please fill in all required fields';
        window.scrollTo(0, 0);
      }
    }
  }
}
</script>
