<template>
  <div class="customer container">
    <div class="row mt-4">
      <Alert class="w-100 text-center" v-if="alert" :message="alert.message" :type="alert.type" />
    </div>
    <div class="row mt-4">
      <h1>Manage Customers</h1>
    </div>
    <div class="row">
      <input type="text" name="search-input" class="form-control my-2" placeholder="Search customer by last name..." autocomplete="off"
        v-model="searchKeyword"
      >
    </div>
    <div class="row mt-4">
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">First Name</th>
            <th scope="col">Last Name</th>
            <th scope="col">Email</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="customer in filteredCustomer" :key="customer._id">
            <td>{{ customer.first_name }}</td>
            <td>{{ customer.last_name }}</td>
            <td>{{ customer.email }}</td>
            <td><router-link :to="'/details/'+customer._id">Details</router-link></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Alert from '../components/Alert';

export default {
  name: "Customer",
  components: {
    Alert
  },
  data() {
    return {
      customers: [],
      alert: {},
      searchKeyword: ''
    }
  },
  computed: {
    filteredCustomer() {
      const capitalizedKey = this.searchKeyword.charAt(0).toUpperCase() + this.searchKeyword.slice(1);
      return this.customers.filter(customer => customer.last_name.includes(capitalizedKey));
    } 
  },
  methods: {
    async fetchCustomers() {
      try {
        const response = await axios.get('api/v1/customer');
        this.customers = response.data.body;
        this.alert = this.$route.query.alert;
      } catch (err) {
        console.log(err);
      }
    }
  },
  created() {
    this.fetchCustomers();
  },
  updated() {
    this.fetchCustomers();
  }
}
</script>