<template>
  <div class="details-customer container">
    <div class="row mt-4">
      <router-link class="text-secondary" to="/">Back</router-link>
    </div>
    <div class="row border-bottom mt-4 d-flex justify-content-between align-items-center">
      <h1>{{ customer.first_name }} {{ customer.last_name }}</h1>
      <span class="control">
        <span><router-link class="btn btn-primary mr-2" :to="'/edit/'+customer._id">Edit</router-link></span>
        <span><button class="btn btn-danger" @click="deleteCustomer($route.params.id)">Delete</button></span>
      </span>
    </div>
    <div class="row mt-4">
      <ul class="list-group w-100">
        <li class="list-group-item"><img src="../assets/telephone.svg" alt="Phone"> {{ customer.phone }}</li>
        <li class="list-group-item"> <img src="../assets/email.svg" alt="Email"> {{ customer.email }}</li>
      </ul>
    </div>
    <div class="row mt-4">
      <ul class="list-group w-100">
        <li class="list-group-item">{{ customer.address }}</li>
        <li class="list-group-item">{{ customer.city }}</li>
        <li class="list-group-item">{{ customer.state }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'DetailsCustomer',
  data() {
    return {
      customer: {}
    }
  },
  methods: {
    async fetchCustomer(id) {
      try {
        const response = await axios.get(`api/v1/customer/${id}`);
        this.customer = response.data.body;
      } catch (err) {
        console.log(err);
      }
    },
    async deleteCustomer(id) {
      try {
        await axios.delete(`api/v1/customer/${id}`)
        this.$router.push({ path: '/', query: { alert: { message: 'Success delete customer', type: 'success' }}});
      } catch (err) {
        console.log(err);
      }
    }
  },
  created() {
    this.fetchCustomer(this.$route.params.id);
  }
}
</script>

